.counter {
    width: 900px;
    display: flex;
    margin: 0 auto;
    justify-content: space-around;

}

.tablo {
    width: 300px;
    height: 200px;
    border: 2px solid aquamarine;
    background-color: black;
    border-radius: 10px;
    margin: 0 auto;
    margin-top: 200px;
}

.displayCheck {
    width: 250px;
    height: 80px;
    border: 1px solid black;
    margin: 0 auto;
    color: black;
    background-color: aqua;
    border-radius: 5px;
    display: grid;
    grid-template-columns: 150px 2fr;
    grid-template-rows: 1fr 1fr;


}

.input {
    width: 80px;

}

.display {
    width: 250px;
    height: 80px;
    border: 1px solid black;
    margin: 0 auto;
    color: black;
    background-color: aqua;
    border-radius: 5px;
    display: flex;
}


.displayEnd {
    width: 250px;
    height: 80px;
    border: 1px solid black;
    margin: 0 auto;
    color: red;
    background-color: aqua;
    border-radius: 5px;
    display: flex;
}

.number {
    font-size: 40px;
    margin: 0 auto;
}

.button {
    width: 250px;
    height: 80px;
    border: 1px solid white;
    margin: 0 auto;
    margin-top: 5px;
    background-color: black;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-around;

}


.button1 {
    width: 100px;
    height: 50px;
    border: 1px solid black;
    background-color: aqua;
    color: black;
    border-radius: 3px;
    align-items: center;
    font-size: 20px;
}

.button1:hover {
    color: green;
}
